*{
    width: 500px;
    text-align: center;
    margin: auto;
    padding-top: 10px;
    color: white;
}
p{
    margin-bottom: 50px;
    margin-top: 50px;
}
.buttons{
    display: flex;
    flex-direction: row;
}
button{
    font-size: 13pt;
    font-weight: bold;
    cursor: pointer;
    margin: 15px;
    border: 5px white solid;
    padding-bottom: 5px;
    background-color: rgb(3, 73, 255)
}

.consentBtn:hover{
    color: rgb(0, 255, 0);
    border:5px rgb(0, 255, 0) solid;
}

.dontBtn:hover{
    color: rgb(255, 0, 0);
    border:5px rgb(255, 0, 0) solid;
}

.popup-arrow{
    visibility: hidden;
}

.popup-overlay{
    background-color: green !important;
    height: 550px;
}
.popupOverlay{
    background-color: red !important;

}

#popup-2{
    background-color: green;
    border: none !important;
}

#popup-4{
    background-color: red;
    margin-right: 20%;
    width: 490px;
    height: 550px;
    border: none !important;
    position: absolute;
    top: 60px !important;
    left: 470px !important;
}

.doNotText{
    margin-right: 30%;
    padding-top: 200px;
    text-transform: capitalize;
    text-decoration: underline;
    font-weight: bolder;
}

.listConsent{
    top: -270px;
    position: absolute;
}

